import React,{Component} from 'react';
import './Body.css';

class Body extends Component{
    render(){
        return(
          <div className="Body">
              <p>{this.props.titulotemas} </p>
         <ol>
              <li>{this.props.texto}</li>
              <li>{this.props.texto2}</li>
              <li>{this.props.texto3}</li>
              <li>{this.props.texto4}</li>
              <li>{this.props.texto5}</li>
           </ol>

          </div>

        );
    }
}

export default Body;